<template>
  <div class="content">
    <el-form :model="ruleForm" :rules="rules" :inline="true" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="username">
        <el-upload
          class="avatar-uploader"
          action="http://localhost:8080/uploadImage"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item label="" prop="username">
        <el-input v-model="this.$store.state.userInfo.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="useremail">
        <el-input v-model="this.$store.state.userInfo.useremail" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex" required>
        <el-radio-group v-model="ruleForm.sex">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" required>
        <el-col >
          <el-form-item prop="date1">
            <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="ruleForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="address">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="academic">
        <el-select v-model="ruleForm.academic" placeholder="">
          <el-option value=""></el-option>
          <el-option value=""></el-option>
          <el-option value=""></el-option>
          <el-option value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-input type="textarea" v-model="ruleForm.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width: 200px;margin-left: 100px" @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')" style="width: 200px"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// import axios from 'axios'
import { reqUploadHeader } from '../api'
/* const imageUpload = param => {
  const url = 'http://localhost:8080/uploadImage'
  // headers
  return axios.post(url, param, {
    headers: { 'content-type': 'multipart/form-data' }
  })
} */
export default {
  name: 'userInfo',
  data () {
    return {
      imageUrl: this.$store.state.userInfo.headerImg,
      ruleForm: {
        username: '',
        name: '',
        region: '',
        phone: '',
        address: '',
        academic: '',
        useremail: '',
        date1: '',
        sex: '',
        desc: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'change' }
        ],
        date1: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
      this.$store.state.userInfo.header = this.imageUrl
      console.log('imageUrl=', this.imageUrl)
      /* var userId = this.$store.state.userInfo._id
      console.log(res.id)
      reqUploadHeader(userId, this.imageUrl) */
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isJPG && isLt2M
    },
    httpRequest (params) {
      console.log(params.file)
      // var userId = this.$store.state.userInfo._id
      const file = params.file
      const fileType = file.type
      console.log(file)
      const isImage = fileType.indexOf('image') !== -1
      const isLt2M = file.size / 1024 / 1024 < 2
      // 
      if (!isImage) {
        this.$message.error('pngjpggif!')
        return
      }
      if (!isLt2M) {
        this.$message.error('2M')
        return
      }
      // console.log(res.id)
      // 
      const form = new FormData()
      // 
      form.append('file', file)
      // action
      // form.append('clientType', 'http://localhost:8080/uploadImage')
      // 
      console.log(form)
      reqUploadHeader(file)
      /* imageUpload({ form, userId })
        .then(res => {
          // 
          const code = res && parseInt(res.code, 10)
          if (code === 200) {
            // xxx
          } else {
            // xxx
          }
        })
        .catch(() => {
          // xxx
        }) */
    }
  }
}
</script>
<style scoped>
  .content{
    /*width: 400px;*/
    padding: 30px 40px;
    background: #d3dce6;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    font-size: 18px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
    border-radius: 50%;
  }
</style>
