<template>
  <div>
    <el-container>
      <el-header>
        <el-header>
          <el-row>
            <el-col :span="8">
              <div class="grid-content bg-purple-light"></div>
            </el-col>
            <el-col :span="8">
              <div class="grid-content bg-purple">
                <router-link to="/login">
                  <el-button type="primary" round>,</el-button>
                </router-link>
              </div>
            </el-col>
          </el-row>
        </el-header>
      </el-header>
      <el-main>
        <div class="user_logon_develop_ym">
          <div style="font-size: 35px"> </div>
          <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px">
            <el-form-item label="    : " prop="username">
              <el-input type="text" v-model="ruleForm.username" placeholder=":"
                        autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="   : " prop="useremail">
              <el-input type="text" v-model="ruleForm.useremail" placeholder=":"></el-input>
            </el-form-item>
            <el-form-item label="   : " prop="pass">
              <el-input type="password" v-model="ruleForm.pass" placeholder=":"
                        autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="  : " prop="checkPass">
              <el-input type="password" v-model="ruleForm.checkPass" placeholder=":"
                        autocomplete="off"></el-input>
            </el-form-item>
            <!--<el-form-item label="    : " prop="userPhone">
              <el-input type="text" v-model.number="ruleForm.userPhone" placeholder=":"></el-input>
            </el-form-item>
            <el-form-item label="  : ">
              <el-input type="text" placeholder=":" style="width:230px"></el-input>
              <el-button type="text"></el-button>
            </el-form-item>-->
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
              <el-button @click="resetForm('ruleForm')">  </el-button>
            </el-form-item>
          </el-form>
        </div>
        <div>
          <router-link to="/logon"></router-link>
        </div>
      </el-main>
      <el-footer>Footer</el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'DataTotal',
  data () {
    var checkUsername = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('!'))
      } else {
        callback()
      }
    }
    var userEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }, 100)
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    /* var checkPhone = (rule, value, callback) => {
      const phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        // Number.isIntegeres6,
        // +

        if (!Number.isInteger(+value)) {
          callback(new Error(''))
        } else {
          if (phoneReg.test(value)) {
            callback()
          } else {
            callback(new Error(''))
          }
        }
      }, 100)
    } */

    return {
      ruleForm: {
        username: '',
        useremail: '',
        pass: '',
        checkPass: ''
        // userPhone: ''
      },
      rules: {
        username: [
          { validator: checkUsername, trigger: 'blur' }
        ],
        useremail: [
          { validator: userEmail, trigger: 'blur' }
        ],
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ]
        /* userPhone: [
          { validator: checkPhone, trigger: 'blur' }
        ] */
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        console.log(valid)
        if (valid) {
          alert('submit!')
          console.log('success submit!!')
        } else {
          console.log('error submit!!')
          console.log(valid)
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style scoped>
  .el-container {
    margin-top: 40px;
    font-size: 18px;
  }
  .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }

  .user_logon_develop_ym {
    width: 500px;
    margin: auto;
  }
</style>
