<template>
    <div class="content">
      <div class="c1">
        <span></span>
      </div>
      <div class="c2">
          <span id="sign-up-btn"><router-link to="/login"></router-link></span>
      </div>
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px">
        <el-form-item label="    : " prop="username">
          <el-input type="text"  v-model="ruleForm.username" placeholder=":" ></el-input>
        </el-form-item>

        <el-form-item label="   : " prop="useremail">
          <el-input type="text" v-model="ruleForm.useremail" placeholder=":" >
          </el-input>
        </el-form-item>

        <el-form-item label=": " prop="checkCode" >
          <div class="checkCode">
            <el-input type="text" autocomplete="off" v-model="ruleForm.checkCode" placeholder="">
              <el-button type="primary" slot="append" style="float: right" @click="getCode">
                {{computeTime>0 ? `(${computeTime}s)` : ''}}
              </el-button>
            </el-input>
            <!--<img src="http://localhost:8080/captcha" @click="getCaptcha" alt="captcha" ref="captcha"/>-->
          </div>
        </el-form-item>

        <el-form-item label="   : " prop="password">
          <el-input type="password" v-model="ruleForm.password" placeholder=":"
                    autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-radio-group v-model="ruleForm.type">
            <el-radio label=""></el-radio>
            <el-radio label="VIP"></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="userRegister('ruleForm')" style="margin-left: 70px"></el-button>
        </el-form-item>
      </el-form>
  </div>
</template>

<script>
import '../assets/css/accounts.css'
import { reqRegister, reqMeilCode } from '../api'
export default {
  name: 'Login',
  data () {
    var userName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var userEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }, 100)
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateCheck = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.mailCode) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      computeTime: 0,
      mailCode: '',
      ruleForm: {
        username: '',
        useremail: '',
        password: '',
        checkCode: ''
      },
      rules: {
        username: [
          { validator: userName, trigger: 'blur' }
        ],
        useremail: [
          { validator: userEmail, trigger: 'blur' }
        ],
        checkCode: [
          { validator: validateCheck, trigger: 'blur' }
        ],
        password: [
          { validator: validatePass, trigger: 'blur' }
        ]
      }
    }
  },

  methods: {
    // 
    getCode () {
      // 
      if (!this.computeTime) {
        // 
        this.computeTime = 60
        this.intervalId = setInterval(() => {
          this.computeTime--
          if (this.computeTime <= 0) {
            // 
            clearInterval(this.intervalId)
          }
        }, 1000)
        const result = reqMeilCode(this.ruleForm.useremail)
        result.then(response => {
          // 
          if (response.err === -1) {
            // 
            if (this.computeTime) {
              this.computeTime = 0
              clearInterval(this.intervalId)
              this.intervalId = undefined
            }
            this.$confirm(response.msg, '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.ruleForm.useremail = ''
            })
          } else { // 
            this.mailCode = response.data
            console.log(this.mailCode)
          }
        })
      }
    },
    userRegister (formName) {
      this.$refs[formName].validate((valid) => {
        // console.log(valid)
        if (valid) {
          const result = reqRegister(this.ruleForm) // Promise
          result.then(response => { // responsepromise
            if (response.code === 0) {
              // 
              // this.$message.success('')
              this.$notify({
                message: '',
                position: 'bottom-right',
                type: 'success'
              })
              this.$router.replace('/login')
            } else {
              // this.$message.warning(response.msg)
              this.$notify({
                message: response.msg,
                position: 'bottom-right',
                type: 'warning'
              })
              this.$refs[formName].resetFields()
              // 
              this.getCaptcha()
            }
          })
        } else {
          console.log(valid)
          this.$message.error('')
          // 
          this.getCaptcha()
          return false
        }
      })
    }
  }
}
</script>
<style scoped>
  .content{
    width: 400px;
    padding: 30px 40px;
    background: #d3dce6;
    /*background: #E9EEF3;*/
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    font-size: 24px;
  }
  .c1{
    font-size: 20px;
    margin: 5px;
  }
  .c2{
    font-size: 15px;
    text-align: right;
    margin-bottom:10px;
  }
</style>
