<template>
  <div>
    <div class="block" style="width: 720px;float: left">
      <!--<div style="margin-bottom: 25px">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/banner' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/dataresource/1' }"><span @click="myClick()"></span></el-breadcrumb-item>
          <el-breadcrumb-item v-if="SatelliteID==='GF5'"><span style="color: #d43c33"></span></el-breadcrumb-item>
          <el-breadcrumb-item v-else-if="SatelliteID==='ZY1E'"><span style="color: #d43c33"></span></el-breadcrumb-item>
          <el-breadcrumb-item v-else-if="SatelliteID==='HJ2A'"><span style="color: #d43c33">2A</span></el-breadcrumb-item>
          <el-breadcrumb-item v-else-if="SatelliteID==='HJ2B'"><span style="color: #d43c33">2B</span></el-breadcrumb-item>
        </el-breadcrumb>
      </div>-->
      <div style="margin-bottom: 25px">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/banner' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/dataresource/1' }"><span @click="myClick()"></span></el-breadcrumb-item>
          <el-breadcrumb-item v-show="SatelliteID==='GF5'"><span style="color: #d43c33"></span></el-breadcrumb-item>
          <el-breadcrumb-item v-show="SatelliteID==='ZY1E'"><span style="color: #d43c33"></span></el-breadcrumb-item>
          <el-breadcrumb-item v-show="SatelliteID==='HJ2A'"><span style="color: #d43c33">2A</span></el-breadcrumb-item>
          <el-breadcrumb-item v-show="SatelliteID==='HJ2B'"><span style="color: #d43c33">2B</span></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNumb">
      </el-pagination>
    </div>
    <div style="float: left;margin-top: 40px">
      <el-button @click="collects()" type="primary"></el-button>
    </div>
    <div style="margin-top:100px;float: right;width: 500px">
      <el-input placeholder="" v-model="value" class="input-with-select" @change="satellites()" style="float: right;width: 200px">
        <el-button type="primary" slot="append" icon="el-icon-search"></el-button>
      </el-input>
      <el-select v-model="select" slot="prepend" placeholder="" style="width:100px;float: right">
        <el-option label="" value="ProductID"></el-option>
        <el-option label="" value="SceneID"></el-option>
        <el-option label="" value="SceneRow"></el-option>
        <el-option label="" value="ProduceTime"></el-option>
        <el-option label="" value="CloudPercent"></el-option>
        <el-option label="" value="CenterLatitude"></el-option>
        <el-option label="" value="CenterLongitude"></el-option>
        <el-option label="" value="Total"></el-option>
      </el-select>
    </div>
    <el-table
      :data="tableData"
      v-loading="loading"
      element-loading-text=""
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      tooltip-effect="light"
      style="width: 100%"
      @select="getRow"
      :header-cell-style="{background:'darkblue',color:'white',fontWeight:'bold'}">
      <el-table-column
        type="selection">
      </el-table-column>
      <el-table-column
        type="index"
        width="80px"
        label=""
        class="title1">
      </el-table-column>
      <el-table-column
        prop="SatelliteID"
        label="">
      </el-table-column>
      <el-table-column
        prop="ProductID"
        label="">
      </el-table-column>
      <el-table-column
        prop="SceneID"
        label="">
      </el-table-column>
      <el-table-column
        prop="SceneRow"
        label="">
      </el-table-column>
      <el-table-column
        prop="ProduceTime"
        label="">
      </el-table-column>
      <el-table-column
        prop="CloudPercent"
        label="%">
      </el-table-column>
      <el-table-column
        prop="CenterLatitude"
        label="">
      </el-table-column>
      <el-table-column
        prop="CenterLongitude"
        label="">
      </el-table-column>
      <el-table-column
        prop="ThumbFile"
        label="">
        <template slot-scope="scope">
          <el-popover
            placement="left"
            title=""
            trigger="hover">
            <img :src="scope.row.ThumbFile" height="400"/>
            <img slot="reference" :src="scope.row.ThumbFile" style="height: 50px;width:50px">
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="" type="index" width="150">
        <template slot-scope="scope">
        <el-button-group>
          <div class="bottom">
            <el-tooltip class="item" effect="dark" content="" placement="bottom-start">
              <el-button type="info" icon="el-icon-info" size="mini"  @click="detail(scope.row)" circle></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="bottom">
              <el-button ref="downloadbutton" type="danger" icon="el-icon-download" size="mini" @click="download(scope.row)" circle></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="bottom-end">
              <el-button type="warning" icon="el-icon-star-off" @click="collect(scope.row)" size="mini" circle></el-button>
            </el-tooltip>
          </div>
        </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :visible.sync="dialogTableVisible">
        <el-row :gutter="20">
          <el-col :span="12" style="padding: 0" >
            <div class="grid-content bg-purple">
              <p class="title"></p>
              <img src="../assets/position.png" height="250" width="450"/>
              <p class="title"></p>
              <div class="image">
                <img class="image" :src="imgSrc" height="200"/>
              </div>
            </div>
          </el-col>
          <el-col :span="12" style="padding: 0" >
            <div class="grid-content bg-purple" style="background: red">
              <el-table :data="xmlData" height="540px" :header-cell-style="{background:'darkblue',color:'white',fontWeight:'bold'}">
                <el-table-column property="attribute" label="" :cell-style="{color:'black',fontWeight:'bold'}"></el-table-column>
                <el-table-column  property="attributeValue" label=""></el-table-column>
              </el-table>
            </div>
          </el-col>
        </el-row>
      <hr/>
      <el-footer>
        <el-button type="info" @click="dialogTableVisible = false" style="float: right"> </el-button>
      </el-footer>
    </el-dialog>

    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNumb">
      </el-pagination>
    </div>
  </div>
</template>

<script>
// import axios from 'axios'
import { reqXmlDetail, reqSatellites, reqDownload, reqCollect, resDelThumb } from '../api' // 
import '../fiters'// 
export default {
  name: 'SatalliteList',
  methods: {
    getRow (row) {
      console.log('row=', row)
      this.collections = row
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (val) {
      console.log(`: ${val}`)
      console.log('select=', this.select)
      this.satellites()
    },
    detail (row) {
      this.xmlData = []
      const result = reqXmlDetail(row.XMLName) // Promise
      result.then(response => { // responsepromise
        for (let item in response) {
          //  xmlData
          this.xmlData.push({ attribute: item, attributeValue: response[item] })
        }
      })
      this.dialogTableVisible = true
      this.imgSrc = row.ThumbFile
    },
    satellites () {
      var SatelliteID = sessionStorage.getItem('SatelliteID')
      console.log('SatelliteID=', SatelliteID)
      var info = {
        satelliteID: SatelliteID,
        select: this.select,
        value: this.value,
        currentPage: this.currentPage,
        pagesize: this.pagesize
      }
      const result = reqSatellites(info) // Promise
      result.then(response => { // responsepromise
        // console.log(' response.data=', response.data)
        this.tableData = response.data.satellites
        this.totalNumb = response.data.count
        this.loading = false
      })
    },
    // 
    dateFormt (fmt, date) {
      var o = {
        'M+': date.getMonth() + 1,
        'd+': date.getDate(),
        'H+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds(),
        'S+': date.getMilliseconds()
      }
      // date.getFullYear()number,
      if (/(y+)/.test(fmt)) {
        // +date.getFullYear()+''number
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
      }
      for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) {
          // String()String(date.getFullYear())
          fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(String(o[k]).length)))
        }
      }
      return fmt
    },
    // 
    collect (row) {
      var date = new Date()
      var time = this.dateFormt('yyyy-MM-dd', date)
      console.log(time)
      row.date = time
      console.log(row)
      var userInfo = {
        _id: this.$store.state.userInfo._id,
        satelliteCollection: row
      }
      if (userInfo._id === undefined) {
        this.$notify({
          message: '',
          position: 'bottom-right',
          type: 'warning'
        })
      } else {
        const result = reqCollect(userInfo)
        result.then(response => { // responsepromise
          this.$notify({
            message: '',
            position: 'bottom-right',
            type: 'success'
          })
        })
      }
    },
    // 
    collects () {
      var userID = this.$store.state.userInfo._id
      if (userID === undefined) {
        this.$notify({
          message: '',
          position: 'bottom-right',
          type: 'warning'
        })
      } else {
        this.collections.forEach(row => {
          var date = new Date()
          var time = this.dateFormt('yyyy-MM-dd', date)
          console.log(time)
          row.date = time
          var userInfo = {
            _id: this.$store.state.userInfo._id,
            satelliteCollection: row
          }
          const result = reqCollect(userInfo)
          result.then(response => { // responsepromise

          })
        })
        this.$notify({
          message: '',
          position: 'bottom-right',
          type: 'success'
        })
      }
    },
    // 
    download (row) {
      // this.loading = true
      this.$notify({
        message: '......',
        position: 'bottom-right',
        type: 'success'
      })
      // 
      var dir = 'D:\\Remote_sensing_images\\'
      // 
      var a = row.ThumbPath.split('/')
      var name = a[2] + '.tar'
      // console.log(name)
      // var filePath = { path: '123.doc' }
      const result = reqDownload(dir, name)
      result.then(response => { // responsepromise
      // console.log(' response.data=', response)
      // Blobmine
        const blob = new Blob([response])//, { type: 'application/tar' }
        // acreateObjectURLblob
        const link = document.createElement('a')
        link.href = window.URL.createObjectURL(blob)
        // download
        link.download = name
        // click
        link.click()
        // revokeObjectURLurl
        window.URL.revokeObjectURL(link.href)
        this.loading = false
      })
    },
    myClick () {
      sessionStorage.setItem('SatelliteID', '')
    }
  },
  mounted () {
    console.log('')
    this.satellites()
    /* setTimeout(() => {
      this.satellites()
    }, 2000) */
  },
  beforeDestroy () {
    var result = resDelThumb()
    result.then(response => {
      console.log(response.data)
    })
  },
  data () {
    return {
      SatelliteID: sessionStorage.getItem('SatelliteID'),
      collections: [],
      imgSrc: '',
      totalNumb: 0,
      tableData3: [],
      currentRow: 1, // 
      currentPage: 1, //  el-pagination 
      pagesize: 10,
      xmlData: [], // xml
      tableData: [],
      dialogTableVisible: false,
      dialogFormVisible: false,
      dialogVisible: false,
      select: '',
      value: '',
      loading: true,
      downloading: true,
      downLoading: false
    }
  }
}
</script>

<style scoped>

  .block{
    margin-top: 80px;
  }
  .demo-image{
    position: absolute;
    left:10px
  }
  .table{
    position: absolute;
    right:10px
  }
  .title{
    color: #d43c33;
    font-size: 0.18rem;
    margin: 0.1rem auto;
    text-align: center;
  }
  .image{
    vertical-align: middle;
    text-align: center;
  }
  .el-pagination{
    /*background: #d43c33;*/
    height: 0.4rem;
  }
  .bottom {
    clear: both;
    text-align: center;
  }
  .title1{
    color: black;
    font-size: 16px;
    text-align: center;
  }
  .el-breadcrumb-item:last-child {
    color: #d43c33;
  }
  .search-div{
    float: right;
  }
</style>
