<template>
  <div class="content">
    <div class="c1">
      <span></span>
      <span>{{this.$store.state.resetUserInfo}}</span>
    </div>
    <div class="c2">
      <span id="sign-up-btn"><router-link to="/register"></router-link></span>
    </div>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px">
      <el-form-item label="    : " prop="username">
        <el-input type="text" v-model="ruleForm.username" placeholder=":" ></el-input>
      </el-form-item>
      <el-form-item label="   : " prop="useremail">
        <el-input type="text" v-model="ruleForm.useremail" placeholder=":" ></el-input>
      </el-form-item>
      <el-form-item label="   : " prop="password">
        <el-input type="password" v-model="ruleForm.password" placeholder=":"
                  autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="  : " prop="checkCode" >
        <div class="checkCode">
          <el-input type="text" autocomplete="off" v-model="ruleForm.checkCode" placeholder="">
          </el-input>
          <img src="http://localhost:8080/captcha" @click="getCaptcha" alt="captcha" ref="captcha"/>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')" style="margin-left: 10px"></el-button>
        <el-button @click="resetPass"> </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import '../assets/css/accounts.css'
import { reqLogin } from '../api'
export default {
  name: 'Login',
  data () {
    var userName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var userEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }, 100)
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateCheck = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== document.cookie.split('=')[1]) { // document.cookie.split('=')[1]
        // console.log(document.cookie.split('=')[1])
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        username: '',
        useremail: '',
        password: '',
        checkCode: ''
      },
      rules: {
        username: [
          { validator: userName, trigger: 'blur' }
        ],
        useremail: [
          { validator: userEmail, trigger: 'blur' }
        ],
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkCode: [
          { validator: validateCheck, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    getCaptcha () {
      this.$refs.captcha.src = 'http://localhost:8080/captcha?time=' + Date.now()
      // this.$store.state.resetUserInfo = 'buhao'
      /* const result = reqCaptcha(Date.now())
      result.then(response => {
        console.log('captcha=', response)
        // sessionStorage.setItem('captcha', response.data)
        response.type('svg')
        return response.data
      }) */
      // var captcha = sessionStorage.getItem('captcha')
      // console.log('captcha=', captcha)
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        console.log(document.cookie.split('=')[1])
        console.log(valid)
        if (valid) {
          const result = reqLogin(this.ruleForm) // Promise
          result.then(response => { // responsepromise
            if (response.code === 0) {
              // uservuexstate
              const user = response.data
              // this.$store.dispatch('recordUser', user)
              // this.$store.state.userInfo = user
              this.$store.commit('saveUserInfo', user)
              sessionStorage.setItem('user_id', user._id)
              sessionStorage.setItem('type', user.type)
              console.log(sessionStorage)
              // 
              /* this.$confirm('', '', {
                confirmButtonText: '',
                type: 'success'
              }) */
              // this.$message.success('')
              this.$router.replace('/banner')
            } else {
              console.log('Captcha=', response.data)
              // 
              this.getCaptcha()
              this.$confirm(',', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              })
              /* this.$message({
                message: ',',
                type: 'warning'
              }) */
            }
          })
        } else {
          this.$message({
            message: '',
            type: 'error'
          })
          // 
          this.getCaptcha()
          return false
        }
      })
    },
    resetPass () {
      this.getCaptcha()
      this.$router.replace('/reset_password')
    }

  }
}
</script>
<style scoped>
.content{
  width: 400px;
  padding: 30px 40px;
  background: #d3dce6;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  font-size: 18px;
}
.c1{
  font-size: 20px;
  margin: 5px;
}
.c2{
  font-size: 15px;
  text-align: right;
  margin-bottom:10px;
}
.checkCode{
   display: flex;
 }
.get-code{
  margin-left: 4px;
  width: 100px;
}
</style>
