<script>
import { mapState } from 'vuex'
export default {
  name: 'HeaderNav',
  computed: {
    ...mapState(['userInfo']) //  this.$store.state.userInfo  userInfo
  },
  data: function () {
    return {
      navList: [
        { name: '/dataresource', navItem: '' },
        { name: '/search', navItem: '' },
        { name: '/databag', navItem: '' },
        { name: '/onlineserve', navItem: '' },
        { name: '/forum', navItem: ' ' }
      ]
    }
  },
  methods: {
    logout () {
      this.$confirm('?', '', {
