<!--template-->
<template>
    <div id="app">
      <HeaderNav></HeaderNav>
      <router-view></router-view>
    </div>
</template>

<script>
import HeaderNav from './components/HeaderNav'
import { reqUserInfo } from './api'

// import { reqUserInfo } from './api'
export default {
  name: 'App',
  mounted () {
    console.log('')
    this.getUser()
  },
  methods: {
    getUser () {
      const result = reqUserInfo(sessionStorage.user_id)
      result.then(response => { // responsepromise
        if (response.code === 0) {
          // uservuexstate
          const user = response.data
          this.$store.commit('saveUserInfo', user)
        }
      })
      // this.$store.commit('getUserInfo')
    }
  },

  components: {
    HeaderNav: HeaderNav
  }
}

</script>
